<template>
  <div class="homepage" ref="homepage">
    <v-header @showSide="show"></v-header>
    <sidebar :sidebarShow="sidebarShow" @hideSidebar="hide" ref="sidebar"></sidebar>
    <mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :topDistance="topDistance"
                 :bottomDistance="bottomDistance"
                 @top-status-change="handleTopChange"
                 @bottom-status-change="handleBottomChange" ref="loadmore">
      <swipe></swipe>
      <homepage-detail @hideSidebar="hide" ref="homePageDetail"></homepage-detail>
      <div slot="top" class="mint-loadmore-top">
        <span v-show="topStatus != 'loading'"></span>
        <span v-show="topStatus == 'loading'">...</span>
      </div>
      <div slot="bottom" class="mint-loadmore-bottom">
        <span v-show="bottomStatus != 'loading'"></span>
        <span v-show="bottomStatus == 'loading'">...</span>
      </div>
    </mt-loadmore>
  </div>
</template>

<script type="text/ecmascript-6">
  import header from '../Header/Header.vue'
  import sidebar from '../Sidebar/Sidebar.vue'
  import swipe from  '../Swipe/Swipe.vue'
  import homepageDetail from '../HomePageDetail/HomePageDetail.vue'
  import axios from 'axios'

  export default {
    data() {
      return {
        sidebarShow: false,                   //
        data: [],                               //
        topDistance: 50,                       //
        bottomDistance: 70,                    //
        bottomStatus: '',                      //
        topStatus: '',                           //
        scroll: ''
      }
    },
    methods: {
      //
      handleBottomChange(status) {
        this.bottomStatus = status;
      },
      //
      handleTopChange(status) {
        this.topStatus = status;
      },
      //
      loadTop() {
        this.$refs.homePageDetail.$emit('refresh')
        this.$refs.loadmore.onTopLoaded()
      },
      //
      loadBottom() {
        this.$refs.homePageDetail.$emit('loadMore')
        this.$refs.loadmore.onBottomLoaded()
      },
      //better-scroll
      show() {
        this.scroll = document.scrollingElement.scrollTop
        this.sidebarShow = true;
        if (this.sidebarShow) {
          this.$nextTick(() => {
            this.$refs.sidebar.fetchData();
          })
          document.body.className = 'modal-open'
          document.body.style.top = -this.scroll + 'px'
        }

      },
      //
      hide() {
        this.sidebarShow = false;
        document.body.className = ""
        document.scrollingElement.scrollTop = this.scroll
      }
    },
    //
    components: {
      'v-header': header,
      sidebar, swipe, homepageDetail
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .mint-loadmore-top
    position relative
    top 40px

  .mint-loadmore-bottom
    position relative
    top 30px

  .modal-open
    position:fixed
    width:100%
</style>



